import React, { useState, useEffect } from 'react';
import { Loader, CheckCircle, AlertCircle } from 'lucide-react';

//  **PROGRESS INDICATOR COMPONENT**: Hin th tin trnh real-time t WebSocket
const ProgressIndicator = ({ 
  progress, 
  className = '' 
}) => {
  const [isVisible, setIsVisible] = useState(true);

  //  **FADE OUT EFFECT**: Hiu ng m dn sau khi completed
  useEffect(() => {
    if (progress) {
      setIsVisible(true); // m bo hin th khi c progress
      
      //  **AUTO FADE OUT**: Nu completed, hin th y  2s ri mi fade out 1.5s
      if (progress.stage === 'completed') {
        console.log(' [ProgressIndicator] Completed detected, starting fade sequence...');
        
        const showCompletedTimeout = setTimeout(() => {
          console.log(' [ProgressIndicator] Starting fade out after 2s display...');
          setIsVisible(false);
        }, 2000); // Hin th y  trong 2 giy trc khi bt u fade
        
        return () => {
          clearTimeout(showCompletedTimeout);
        };
      }
    }
    // Khng n progress khi progress = null,  component cha quyt nh
  }, [progress]);

  //  **PROGRESS BAR**: Hin th thanh tin trnh
  const renderProgressBar = () => {
    if (!progress) return null;

    const { percent = 0, stage = 'processing' } = progress;
    
    //  **STAGE COLORS**: Mu sc theo stage
    const getStageColor = () => {
      switch (stage) {
        case 'initializing':
          return 'bg-blue-500';
        case 'processing':
          return 'bg-green-500';
        case 'completed':
          return 'bg-green-600';
        case 'error':
          return 'bg-red-500';
        default:
          return 'bg-gray-500';
      }
    };

    //  **STAGE ICON**: Icon theo stage
    const getStageIcon = () => {
      switch (stage) {
        case 'initializing':
          return <Loader className="w-4 h-4 animate-spin" />;
        case 'processing':
          return <Loader className="w-4 h-4 animate-spin" />;
        case 'completed':
          return <CheckCircle className="w-4 h-4" />;
        case 'error':
          return <AlertCircle className="w-4 h-4" />;
        default:
          return <Loader className="w-4 h-4 animate-spin" />;
      }
    };

    return (
      <div className="space-y-2">
        {/*  **PROGRESS INFO**: Thng tin tin trnh */}
        <div className="flex items-center justify-between text-sm">
          <div className="flex items-center gap-2">
            {getStageIcon()}
            <span className="font-medium capitalize">{stage}</span>
          </div>
          <span className="font-mono">{Math.round(percent)}%</span>
        </div>

        {/*  **PROGRESS BAR**: Thanh tin trnh */}
        <div className="w-full bg-gray-200 rounded-full h-2 overflow-hidden">
          <div 
            className={`h-full transition-all duration-300 ease-out ${getStageColor()}`}
            style={{ width: `${Math.min(100, Math.max(0, percent))}%` }}
          />
        </div>

        {/*  **PROGRESS MESSAGE**: Thng ip tin trnh */}
        {progress.message && (
          <div className="text-xs text-gray-600">
            {progress.message}
          </div>
        )}

        {/*  **ADDITIONAL INFO**: Thng tin b sung */}
        {progress.currentTime && (
          <div className="text-xs text-gray-500">
            Current: {progress.currentTime}
            {progress.targetSize && ` | Size: ${progress.targetSize}`}
          </div>
        )}

        {/*  **ERROR MESSAGE**: Hin th li nu c */}
        {stage === 'error' && progress.error && (
          <div className="text-sm text-red-600 bg-red-50 p-2 rounded">
            Error: {progress.error}
          </div>
        )}
      </div>
    );
  };

  //  **MAIN RENDER**: Render chnh
  if (!progress) {
    return null; // Khng hin th g nu khng c progress
  }

  return (
    <div 
      className={`
        bg-white border rounded-lg p-4 shadow-sm 
        transition-opacity duration-[1500ms] ease-out
        ${isVisible ? 'opacity-100' : 'opacity-0'}
        ${className}
      `}
    >
      {/*  **PROGRESS BAR**: Hin th tin trnh */}
      {renderProgressBar()}
    </div>
  );
};

export default ProgressIndicator; 